<template>
  <div class="hello">
    <h1>Conversation Flow Dispatcher</h1>
    <div class="console" ref="console" v-html="console"></div>
    <small>INTRO</small>
    <div>
      <p>Click Confirm All to send the current flows.</p>
      <p>Click Confirm to send one flow.</p>
      <p>Click Add Flow to add a new flow.</p>
    </div>
    <div class="row">
      <div class="col col-6">
        <button @click="addFlow()">Add Flow</button>
        <button @click="addFiveSecondFlows()">Add 5s Delayed Flow</button>
        <button @click="confirmAllFlows()">Confirm All</button>
        <hr/>
        <div class="flow" v-for="(f, fidx) in flow">
          <input type="text" v-model="f.name"/>
          <textarea v-model="f.message"></textarea>
          <input type="text" v-model="f.condition" placeholder="beacon,^engaged"/>
          <small>DELAY (seconds, keep 0 for instant send)</small>
          <input type="number" v-model="f.delay"/>
          <button @click="confirmFlow(fidx)" v-show="!f.confirmed">Confirm</button>
        </div>
      </div>
      <div class="col col-6">
        <small>PHONE BOOK</small>
        <div class="phonebook">
          <table>
            <tr v-for="user in users">
              <td>{{ user.number }}</td>
              <td><small v-for="tag in user.tags">#{{ tag }}</small></td>
            </tr>
          </table>
        </div>
        <small>TAGS</small>
        <div class="tags-list">
          <span v-for="tag in tagsAvailable" class="tag">{{ tag }}</span>
        </div>
        <small>FLOWS</small>
        <textarea class="output" :value="jsonOutput" readonly></textarea>
        <small>HISTORY</small>
        <textarea class="output" :value="historyOutput" readonly></textarea>
      </div>
    </div>
  </div>
</template>

<script>
function parseOutcome (tags, condition) {
  var chunks = condition === true ? [] : condition.split(',')
  return chunks.reduce((acc, c) => {
    var query = c[0] === '^'? tags.indexOf(c.slice(1)) == -1 : tags.indexOf(c) !== -1
    acc = acc && query
    return acc
  }, true)
}

export default {
  name: 'HelloWorld',
  mounted() {
    setInterval(this.checkUsers, 1000)
    this.fire('-system initialized-')
  },
  data () {
    return {
      since: new Date().getTime(),
      flow: [
        {
          name: 'Initial Push 1/2',
          message: 'Almost release of DSE result la!',
          condition: 'beacon',
          confirmed: true
        },
        {
          name: 'Initial Push 2/2',
          message: 'Remember to check your scores.',
          condition: 'beacon',
          confirmed: true
        }
      ],
      users: [
        {
          number: '+852 1234 5678',
          tags: ['beacon']
        },
        {
          number: '+852 4567 8901',
          tags: ['beacon','engaged']
        },
        {
          number: '+852 3456 7890',
          tags: ['engaged']
        },
        {
          number: '+852 4567 8901',
          tags: ['beacon']
        },
        {
          number: '+852 5678 9012',
          tags: ['beacon']
        }
      ],
      console: '',
      history: [],
      delay: {}
    }
  },
  methods: {
    elapsedTime() {
      var elapsed = (new Date().getTime() - this.since) / 1000
      return Math.floor(elapsed % 60)
    },
    addFlow () {
      this.flow.push({
        name: 'New Flow',
        message: 'Add Your Message',
        condition: '',
        confirmed: false,
        delay: 0
      })
    },
    confirmFlow (n) {
      if(this.flow[n].delay == 0) {
        delete this.flow[n].delay
      }
      this.$set(this.flow[n], 'confirmed',  true)
    },
    confirmAllFlows() {
      for(var i = 0; i < this.flow.length; i++) {
        this.confirmFlow(i)
      }
    },
    scrollConsoleDown () {
      const consoleElem = this.$refs['console']
      consoleElem.scrollTop = consoleElem.scrollHeight - consoleElem.clientHeight
    },
    fire (str) {
      this.console = this.console.concat('<div>'+str+'</div>')
      this.scrollConsoleDown()
    },
    moveFlowToHistory(name) {
      var payload = this.flow.shift()
      payload.sentTimestamp = new Date().getTime()
      this.history = this.history.concat()
      if(name) {
        delete this.delay[name]
      }
    },
    checkUsers() {
      if(this.flow.length == 0 || this.flow.length > 0 && !this.flow[0].confirmed) {
        // this.fire('-no action-')
      }
      for(var i = 0; i < this.flow.length; i++) {
        var hasDelay = this.flow[i].name in this.delay
        for(var j = 0; j < this.users.length; j++) {
          var result = parseOutcome(this.users[j].tags, this.flow[i].condition)
          if(!hasDelay && result && this.flow[i].confirmed || hasDelay && this.delay[this.flow[i].name] == 1 && result && this.flow[i].confirmed) {
            this.fire([this.elapsedTime(), this.users[j].number,this.flow[i].message].join(' '))
          }
        }
        if(!this.flow[i].delay && this.flow[i].confirmed || this.flow[i].delay && this.delay[this.flow[i].name] == 1) {
          this.moveFlowToHistory(this.flow[i].name)
        }
        if(hasDelay) {
          this.delay[this.flow[i].name]--
        }
        else if (this.flow[i].delay) {
          this.delay[this.flow[i].name] = this.flow[i].delay
        }
      }
    },
    addFiveSecondFlows() {
      this.flow = this.flow.concat([
        {
          name: 'Five Second Flow 1',
          message: 'Here it goes, 5s flows!',
          condition: true,
          confirmed: true
        },
        {
          name: 'Five Second Flow 2',
          message: 'Is this sent 5 seconds later?',
          condition: true,
          confirmed: true,
          delay: 5
        }
      ])
    }
  },
  computed: {
    jsonOutput() {
      return JSON.stringify(this.flow, null, 2)
    },
    historyOutput() {
      return JSON.stringify(this.history, null, 2)
    },
    tagsAvailable() {
      return this.users.reduce((acc, u) => {
        acc = acc.concat(u.tags)
        return acc
      }, [])
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
h1, h2 {
  font-weight: normal;
}
ul {
  list-style-type: none;
  padding: 0;
}
li {
  display: inline-block;
  margin: 0 10px;
}
a {
  color: #42b983;
}
input, textarea {
  display: block;
  width: 100%;
  outline: none;
}
.row {
  display: flex;
}
.col {
  margin: 0 1rem;
}
.col-6 {
  width: 50%;
}
.flow {
  margin-bottom: 1rem;
}
.output {
  height: 5rem;
}
.console {
  background: #000;
  text-align: left;
  color: #fff;
  padding: 1rem;
  width: 100%;
  height: 8rem;
  margin-bottom: 2rem;
  overflow-y: auto;
  font-family: monospace;
}
.tags-list {
  margin: 1rem;
}
.tag {
  background: #222;
  color: #fff;
  padding: 2px 4px;
  margin: 2px;
}
</style>



