<template>
  <div>
    <textarea v-model="userInput"></textarea>
    <button @click="analyze()">Analyze</button>
    <button @click="flag()">Flag it</button>
    <div>
      <h3>All keywords</h3>
      <div class="word-list">
        <span class="word" v-for="w in result">{{w}}&nbsp;</span>
      </div>
      <div class="word-list">
        <span class="entity" v-for="e in entities">[{{e}}]&nbsp;</span>
      </div>
      <div v-for="f in flaggingResult" class="flag" :class="{'positive': f.outcome}">
        <div><strong>Keywords:</strong> {{ f.keywords && f.keywords.join(', ') }}</div>
        <div><strong>Topics:</strong> {{ f.entities && f.entities.join(', ') }}</div>
        {{ f.outcome }}
      </div>
    </div>
  </div>
</template>

<script>

export default {
  name: 'Analyzer',
  mounted() {
    const urlParams = new URLSearchParams(window.location.search)
    const myParam = urlParams.get('q')
    this.userInput = myParam
    this.analyze()
    var self = this
    setTimeout(() => {
      self.flag()
    }, 500)

  },
  data () {
    return {
      userInput: "",
      topics: [],
      categories: [],
      entities: [],
      result: [],
      flaggingResult: [],
      rules: [
        {
          keywords: ["kindly pay"],
          entities: ["Computer virus", "Error"]
        }
        ,
        {
          keywords: ["word"],
          entities: ["Microsoft"]
        }
        ,
        {
          keywords: ["excel"],
          entities: ["Microsoft"]

        },
        {
          keywords: ["payment","advice"],
          entities: ["Customer"]
        },
        {
          keywords: ["confirmation", "attached"],
          entities: ["Payment"]
        },
        {
          keywords: ["account", "address"],
          entities: []
        },
        {
          keywords: ["standby","undersigned"],
          entitles: ["Letter of credit"]
        },
        {
          keywords: ["virus", "firewall"],
          entities: []
        }
      ]
    }
  },
  methods: {
    analyze() {
      this.result = []
      this.entities = []
      var self = this
      fetch('https://fishvisor-extract.glitch.me/keywords?text='+encodeURIComponent(this.userInput)).then(res => {
        res.json().then(data => {
          self.result = data
        })
      })
      fetch('https://fishvisor-extract.glitch.me/nlp?text='+encodeURIComponent(this.userInput)).then(res => {
        res.json().then(data => {
          self.entities = data.response.entities.map(e => e.entityEnglishId).filter(e => e.length > 0)
        })
      })
    },
    containKeyword(w, body) {
      return body.match(/\w+/g).map(w => w.toLowerCase()).indexOf(w.toLowerCase()) !== -1
    },
    containTopic(t, matched) {
      return matched.indexOf(t) !== -1
    },
    flag() {
      this.flaggingResult = []
      for(var i=0; i<this.rules.length; i++) {
        var result = {
          keywords: this.rules[i].keywords,
          entities: this.rules[i].entities,
          outcome: this.validate(this.userInput, this.entities, this.rules[i])
        }
        this.flaggingResult.push(result)
        if(this.flaggingResult.filter(f=>f.outcome).length == 0) {
          this.flaggingResult = []
        }
      }
    },
    validate(body, entities, rule) {
      var self = this
      return rule.keywords.reduce((acc, kw) => {
        acc = acc && self.containKeyword(kw, body)
        return acc
      }, true) && entities.reduce((acc, en) => {
        acc = acc || self.containTopic(en, entities)
        return acc
      }, false)
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.word-list {
  margin: 1rem;
}
.word {
  background: #222;
  color: #fff;
  padding: 2px 4px;
  margin: 2px;
}

.entity {
  color: #aaa;
  padding: 2px 4px;
  margin: 2px;
}

.flag {
  background: #aaa;
  padding: 1rem;
  margin-bottom: .5rem;
}

.flag.positive {
  background: #00ff6b;
}
</style>



